@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

body {
  background-color: #0a192f;

  
}

.App {
  margin: 0%;

}

.container {
  position: fixed;
  background-color: #0a192f;
  height: 70px;
  width: 100%;
  display: flex;
  align-items: center;
}

.nav-wrapper {
  display: flex;
  align-items: center;
  height: 100%;
  width: 100%;
  padding-left: 30px;
  padding-right: 30px;
}
.list-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.links {
  display: flexbox;
  text-align: center;
  height: auto;
  width: 100px;
  color: aquamarine;
  text-decoration: none;

  font-family: "Roboto Mono", monospace;

  font-style: normal;
  font-weight: 400;
  font-size: 15px;
}


.img-container {
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 190px;
  font-size: 50px;
  text-decoration: overline;
  font-family: "Montserrat", sans-serif;


  color: aquamarine;
}


.wrapper-title-name {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 190px;
}

.title-name {
  text-align: center;
  width: 80%;
  color: aquamarine;
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  font-family: "Montserrat", sans-serif;
}

